<!-- eslint-disable max-len -->
<template>
  <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">
        Каталог
      </h1>
      <span class="content__info">
        152 товара
      </span>
    </div>

    <div class="content__catalog">
      <ProductFilter :price-from.sync="filterPriceFrom"
       :price-to.sync="filterPriceTo"
       :category-id.sync="filterCategoryId"
       :color-id.sync="filterColorCode" />
      <section class="catalog">
        <div v-if="productsLoading">Загрузка товаров...</div>
        <div v-if="productsLoadingFailed">Произошла ошибка при загрузке товара <button @click.prevent="loadProducts">Попробовать еще раз</button></div>
        <ProductList :products="products"/>
          <!-- <ProductList :products="products" @gotoPage="(pageName, pageParams) => $emit('gotoPage', pageName, pageParams)"/> -->

        <BasePagination v-model="page" :count="countProducts" :per-page="productsPerPage" />

      </section>

    </div>
  </main>
</template>
<script>
// import products from '@/data/products';
import ProductList from '@/components/ProductList.vue';
import BasePagination from '@/components/BasePagination.vue';
import ProductFilter from '@/components/ProductFilter.vue';
import axios from 'axios';
import { API_BASE_URL } from '../store/config';

export default {
  components: { ProductList, BasePagination, ProductFilter },
  data() {
    return {
      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterCategoryId: 0,
      filterColorCode: 0,

      page: 1,
      productsPerPage: 3,

      productsData: null,
      productsLoading: false,
      productsLoadingFailed: false,

      colorsData: null,
      colorsLoading: false,
      colorsLoadingFailed: false,
    };
  },
  computed: {
    products() {
      return this.productsData
        ? this.productsData.items.map((product) => ({
          ...product,
          image: product.image.file.url,
        }))
        : [];
    },
    // colors() {
    //   return this.colorsData
    //     ? this.colorsData.items.map((colors) => ({
    //       ...colors,
    //     }))
    //     : [];
    // },
    countProducts() {
      return this.productsData ? this.productsData.pagination.total : 0;
    },
  },
  methods: {
    loadProducts() {
      this.productsLoading = true;
      this.productsLoadingFailed = false;
      clearTimeout(this.loadProductsTimer);
      this.loadProductsTimer = setTimeout(() => {
        // eslint-disable-next-line no-template-curly-in-string
        axios.get(`${API_BASE_URL}/api/products`, {
          params: {
            page: this.page,
            limit: this.productsPerPage,
            categoryId: this.filterCategoryId,
            minPrice: this.filterPriceFrom,
            maxPrice: this.filterPriceTo,
            colorId: this.filterColorCode,
          },
        })
        // eslint-disable-next-line no-return-assign
          .then((response) => this.productsData = response.data)
          // eslint-disable-next-line no-return-assign
          // Отлавливаем ошибку
          // eslint-disable-next-line no-return-assign
          .catch(() => this.productsLoadingFailed = true)
          // eslint-disable-next-line no-return-assign
          .then(() => this.productsLoading = false);
      }, 0);
    },
    // loadColors() {
    //   this.colorsLoading = true;
    //   this.colorsLoadingFailed = false;
    //   clearTimeout(this.loadColorsTimer);
    //   this.loadColorsTimer = setTimeout(() => {
    //     // eslint-disable-next-line no-template-curly-in-string
    //     axios.get(`${API_BASE_URL}/api/colors`, {
    //     })
    //     // eslint-disable-next-line no-return-assign
    //       .then((response) => this.colorsData = response.data)
    //       // eslint-disable-next-line no-return-assign
    //       // Отлавливаем ошибку
    //       // eslint-disable-next-line no-return-assign
    //       .catch(() => this.colorsLoadingFailed = true)
    //       // eslint-disable-next-line no-return-assign
    //       .then(() => this.colorsLoading = false);
    //   }, 0);
    // },
  },
  watch: {
    page() {
      this.loadProducts();
    },
    filterPriceFrom() {
      this.loadProducts();
    },
    filterPriceTo() {
      this.loadProducts();
    },
    filterCategoryId() {
      this.loadProducts();
    },
    filterColorCode() {
      this.loadProducts();
    },

  },
  created() {
    this.loadProducts();
  },
};
</script>
